@charset "UTF-8";
@import url(../../dist/css/normalize.css);
/* Reset */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  font-family: inherit;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

@font-face {
  font-family: "Sora-Regular";
  src: url("../../src/assets/fonts/Sora-Regular.ttf");
}
@font-face {
  font-family: "Sora-Bold";
  src: url("../../src/assets/fonts/Sora-Bold.ttf");
}
@font-face {
  font-family: "Sora-ExtraBold";
  src: url("../../src/assets/fonts/Sora-ExtraBold.ttf");
  font-weight: 800;
}
@font-face {
  font-family: "Sora-SemiBold";
  src: url("../../src/assets/fonts/Sora-SemiBold.ttf");
}
body {
  font-family: "Sora-Regular", sans-serif;
  font-size: 16px;
  color: #71717A;
}

.header {
  padding: 24px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
@media (max-width: 768px) {
  .header__nav, .header__button, .header .hvr-grow-shadow {
    display: none;
  }
}
.header .header__button {
  text-align: center;
  width: 153px;
  padding: 16px 0;
  background: #000000;
  border-radius: 4px;
  cursor: pointer;
}
.header .header__button:hover .header__button-button {
  color: #5968BA;
  transition: all 0.3s ease;
}
.header .header__button .header__button-button {
  font-family: "Sora-SemiBold", sans-serif;
  font-size: 20px;
  color: #ffffff;
  background: transparent;
  border: none;
}
.header .block_flip-burger {
  display: none;
  cursor: pointer;
  z-index: 1001;
}
@media (max-width: 768px) {
  .header .block_flip-burger {
    display: block;
  }
}
.header .block_flip-burger .flip-burger {
  width: 36px;
  height: 35px;
  perspective: 1000px;
}
.header .block_flip-burger .flip-burger-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}
.header .block_flip-burger .flip-burger-inner.flipped {
  transform: rotateY(180deg);
}
.header .block_flip-burger .flip-burger-front, .header .block_flip-burger .flip-burger-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}
.header .block_flip-burger .flip-burger-back {
  transform: rotateY(180deg);
}
.header .header__logo .logo {
  perspective: 1000px;
  width: 140px;
  height: 20px;
}
.header .header__logo .logo-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  animation: flip 10s infinite;
}
.header .header__logo .logo-front, .header .header__logo .logo-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .header__logo .logo-back {
  transform: rotateY(180deg);
}
.header .header__logo .logo .logo-img {
  max-width: 100%;
  max-height: 100%;
}
@keyframes flip {
  0% {
    transform: rotateY(0deg);
  }
  10% {
    transform: rotateY(180deg);
  }
  50% {
    transform: rotateY(180deg);
  }
  60% {
    transform: rotateY(360deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
.header .header__nav .header__nav-list {
  display: flex;
}
.header .header__nav .header__nav-list .header__nav-item {
  margin: 0 20px;
}
.header .header__nav .header__nav-list .header__nav-item .header__nav-item-link {
  font-family: "Sora-SemiBold", sans-serif;
  font-size: 20px;
  color: #000000;
