.header .header__nav .header__nav-list .header__nav-item .header__nav-item-link:hover {
  color: #5968BA;
  transition: all 0.3s ease;
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: #fff;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transform: translateY(-100%);
  transition: transform 0.3s ease-in-out;
}
.mobile-menu.open {
  transform: translateY(0);
}
.mobile-menu .mobile-nav-list {
  list-style: none;
  padding: 0;
  text-align: center;
}
.mobile-menu .mobile-nav-item {
  margin: 20px 0;
}
.mobile-menu .mobile-nav-link {
  text-decoration: none;
  color: #5968BA;
  font-size: 20px;
  transition: color 0.3s;
}
.mobile-menu .mobile-nav-link:hover {
  color: #007bff;
}
.mobile-menu .mobile-button-wrapper {
  width: 153px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 16px 0;
  gap: 8px;
  background: #000000;
  border-radius: 4px;
  margin-top: 30px;
}
.mobile-menu .mobile-button-wrapper .mobile-button {
  font-family: "Sora-SemiBold", sans-serif;
  font-size: 20px;
  color: #5968BA;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
}

