.contact .contact__description .contact__description-email {
  font-family: "Sora-SemiBold", sans-serif;
  font-size: 28px;
  font-weight: 600;
  line-height: 114%;
  letter-spacing: -0.02em;
  color: #000000;
}

.footer {
  padding: 30px 0;
  background: #000000;
}
.footer .footer__items {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}
.footer .footer__items .footer__logo .logo {
  perspective: 1000px;
  width: 140px;
  height: 20px;
}
.footer .footer__items .footer__logo .logo-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transform-style: preserve-3d;
  animation: flip 10s infinite;
}
.footer .footer__items .footer__logo .logo-front, .footer .footer__items .footer__logo .logo-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.footer .footer__items .footer__logo .logo-back {
  transform: rotateY(180deg);
}
.footer .footer__items .footer__logo .logo .logo-img {
  max-width: 100%;
  max-height: 100%;
}
@keyframes flip {
  0% {
    transform: rotateY(0deg);
  }
  10% {
    transform: rotateY(180deg);
  }
  50% {
    transform: rotateY(180deg);
  }
  60% {
    transform: rotateY(360deg);
  }
  100% {
    transform: rotateY(360deg);
  }
}
.footer .footer__items .footer__item-text {
  color: #ffffff;
}

/* Reset */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  font-family: inherit;
}

